:root {
  --white: white;
  --background-dark-purple: #583eec;
  --background-light-purple: #6B54EE;
  --silver-primary: #dddddd;
  --secondary: #e88176;
  --dark: #333333;
  font-size: 16px;
  ---width-xl: 1140px;
  ---width-lg: 940px;
  ---width-md: 720px;
  ---width-sm: 540px;
  ---width-xs: 340px;
  --transition-time-by-default: 1s;
  -webkit-transition: all .3s ease-in-out;
  -moz-transition: all .3s ease-in-out;
  -ms-transition: all .3s ease-in-out;
  -o-transition: all .3s ease-in-out;
  transition: all .3s ease-in-out;
}
* {
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: -moz-none;
  -o-user-select: none;
  user-select: none;
}

input,
textarea,
button,
select,
svg,
img,
li,
a {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
p {
  -webkit-user-select: text;
  -khtml-user-select: text;
  -moz-user-select: text;
  -o-user-select: text;
  user-select: text;
}

.st0{fill:#322F42;}

.st1{fill:#D6111E;}

h1 {
  color: #52322B;
}


body {
  font-family: sans-serif;
  padding: 0;
  margin: 0;
}
html, body {
  overflow-x: hidden !important;
  overflow-y: hidden;
}
#configurator {
 z-index: 15;
 position: absolute;
 width: 100vw;
 bottom: -10px;
display: flex;
justify-content: center;
}

#webgi-canvas {
  width: 100%;
	height: 100vh;
	margin: 0;
	padding: 0;
	position: fixed;
	top: 0;
	left: 0;
}
#webgi-canvas-container {
  width: 100vw;
	height: 100vh;
	margin: 0;
	padding: 0;
	position: fixed;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  position: fixed;
  display: flex;
  pointer-events: none;
  justify-content: flex-end;
  flex-direction: column;
  align-items: center;
}
.loader {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: #ffffff;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 2em;
  font-size: 12px;
  transition: 4s;
}
.progress {
  position: absolute;
  top: 60%;
  width: 50%;
  height: 1px;
  background: #52322B;
  transform: scaleX(0);
  animation: progresss 1s;
  transform-origin: left;
  z-index: 10;
  margin: 0 5%;
}
@keyframes progresss {
  0% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(100%);
  }
}

/* -------------- HEADER -------------- */
.header {
  height: auto;
  padding: 2rem;
  position: fixed;
  z-index: 2;
  width: 100vw;
}
.navbar-nav {
  padding: 0 4rem;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.header-brand {
  color: #52322B;
  font-family: "Archivo", sans-serif;
  font-weight: 200;
  font-size: 1.5625rem;
  line-height: 100%;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  z-index: 12;
}
.header-brand > a > img {
  width: 210px;
}
.header-brand > a {
  text-decoration: none;
  font-size: 23px;
  font-family: 'Lato', sans-serif;
  color: #312F42;
  text-align: center;
  font-weight: 500;
  letter-spacing: 1px;
}

.header-menu {
  margin: auto 0 !important;
  list-style: none;
  flex-direction: row;
  display: inline-flex;
  align-items: center;
}
.header-menu > button {
  margin: 0 auto;
}
