/* -------------- SECTION -------------- */
.section {
	position: relative;
	z-index: 1;
	display: flex;
	width: 100vw;
	height: 100vh;
	align-content: center;
	margin: 0;
	padding: 0;
	scroll-snap-align: start;
	-moz-user-select: none;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	overflow: hidden;
}


/* -------------- SECTION 1 -------------- */

.section-1-container {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 50vw;
    top: 0;
    position: absolute;
    right: 0;
    min-height: 100vh;
    opacity: 0;
    z-index: 2;
}
.section-1-content {
  width: 100%;
  max-width: 500px;
  margin-right: 30%;
}
.section-1-content > h1 {
  position: relative;
  left: -12px;
  font-family: "Playfair Display SC", serif;
  font-weight: 400;
  font-size: 7.75rem;
  line-height: 95%;
  letter-spacing: -0.04em;
  color: #52322B;
  text-align: right;
  margin-bottom: 40px;
}
.section-1-text > p {
  font-weight: 200;
  font-size: 0.8125rem;
  line-height: 180%;
  text-align: right;
  margin-bottom: 30px;
  color: #52322B;
}
.section-1-text > button {
  background-color: transparent;
  width: 155px;
  height: 60px;
  border: 1px solid #52322B;
  border-radius: 58px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-weight: 200;
  font-size: 0.9375rem;
  letter-spacing: -0.04em;
  color: #52322B;
  float: right;
  cursor: pointer;
  transition: all 0.8s ease-in-out;
  gap: 1rem;
  flex-direction: row;
  cursor: pointer;
  transition: all 0.8s ease-in-out;
}
.section-1-text > button > a {
  text-decoration: none;
  font-style: inherit;
  color: #52322B;
}
.section-1-text > button:hover {
  box-shadow: inset 0 0 60px #c04040;
	color: white;
	transform: translateY(-3px);
}

