.footer-container {
  z-index: 12;
  position: fixed;
  opacity: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 5rem;
  align-items: center;
  width: 100vw;
  height: 5rem;
  margin-bottom: 3%;
  bottom: 0;
  left: 0;
}
.footer-container > li {
  list-style: none;
  cursor: pointer;
  transition: 0.6s ease;
}
.footer-diamond-colors {
  pointer-events: none;
}
.footer-ring-colors {
  pointer-events: none;
}
.footer-container > li:hover {
  transform: scale(1.3);
}
.footer-container-diamond-color {
  position: absolute;
  border: 1px solid;
  bottom: 90px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.8784313725);
  border-radius: 50px;
  padding: 20px 30px;
  opacity: 0;
  visibility: hidden;
  display: flex;
  transition: all 0.4s ease-in-out;
  max-width: 90%;
  z-index: 10;
  list-style: none;
  overflow-x: scroll;
  overflow-y: hidden !important;
}
.footer-container-diamond-color > li {
  padding: 0 1rem 0 1rem;
  pointer-events: all;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer-container-diamond-color > li > img {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
   -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Old versions of Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
          user-select: none; 
  transition: 0.5s;
}
.footer-container-diamond-color > li > img:hover {
  transform: scale(1.2);
}
.footer-container-ring-colors {
  position: absolute;
  border: 1px solid;
  bottom: 90px;
  margin: 0 auto;
  background: rgba(255, 255, 255, 0.8784313725);
  border-radius: 50px;
  padding: 20px 30px;
  opacity: 0;
  visibility: hidden;
  display: flex;
  transition: all 0.4s ease-in-out;
  z-index: 10;
  list-style: none;
  overflow-x: scroll;
  overflow-y: hidden !important;
}
.footer-container-ring-colors > li {
  padding: 0 1rem 0 1rem;
  pointer-events: all;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;

}
.footer-container-ring-colors > li > img {
  transition: 0.5s;
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
   -khtml-user-select: none; /* Konqueror HTML */
     -moz-user-select: none; /* Old versions of Firefox */
      -ms-user-select: none; /* Internet Explorer/Edge */
          user-select: none; 
}
.footer-container-ring-colors > li > img:hover {
  transform: scale(1.2);
}
