/* RESPONSIVE */



@media (max-width: 1200px) {
  .navbar-nav {
    max-width: var(---width-xl);
  }
}
@media (max-width: 970px) {
  .navbar-nav {
    max-width: var(---width-lg);
  }
  .btn-exit {
    width: 95px !important;
    height: 40px !important;
  }
  .footer-container-diamond-color {
    width: 75%;
  }
}
@media (max-width: 760px) {
  .section-1-content {
    max-width: 550px;
    margin-right: 25%;
  }
  .section-1-content > h1 {
    font-size: 5.7rem;
  }
  .section-3-text > h1 {
    font-size: 5rem;
  }
  .navbar-nav {
    max-width: var(---width-md);
  }
  .header-brand > a > img {
    width: 210px;
  }
}
@media (max-width: 570px) {
  .header {
    padding: 1rem;
  }
  .navbar-nav {
    padding: 0 2rem;
  }
  .section-1-content {
    max-width: 600px;
    margin-right: 20%;
  }
