    font-size: 4.2rem;
  }
  .section-3-text > h1 {
    font-size: 4rem;
  }
  .navbar-nav {
    max-width: var(---width-sm);
  }
  .header-brand > a > img {
    width: 210px;
  }
  .section-2-content {
    width: 320px;
    padding: 0;
    border: 0;
