.section-2-title > h1 {
  margin: auto;
  position: relative;
  left: -4px;
  font-family: "Playfair Display SC", serif;
  font-weight: 400;
  font-size: 4rem;
  line-height: 69%;
  letter-spacing: -0.04em;
  padding: 20px 10px 30px 10px;
}
.section-2-content > p {
  color: #52322B;
  font-weight: 200;
  font-size: 0.8125rem;
