}

}
@media (max-width: 500px) {
  .header-brand > a > img {
    width: 180px;
  }
  .header-brand > a {
    font-size: 18px;
  }
}


@media (max-width: 400px) {
  .header {
    padding: 0.5rem 1rem;
  }
  .header-brand {
    flex: 3;
  }
  .header-menu {
    flex: 2;
  }
  .header-brand > a {
    font-size: 16px;
  }
  .navbar-nav {
    padding: 0 1rem;
  }
  .section-1-content {
    max-width: 650px;
    margin-right: 20%;
  }
  .section-1-content > h1 {
    font-size: 3rem;
  }
  .section-3-text > h1 {
    font-size: 3rem;
  }
  .navbar-nav {
    max-width: var(---width-xs);
  }
  .footer-container-ring-colors {
